import { Kategori } from '../../models/kategori';
import { Aktivitet } from '../../models/aktivitet';
import { stringArrayToItems } from './utils';
export function getSkredTing(valg) {
  if (![Aktivitet.Randone, Aktivitet.Fjellski].some(function (it) {
    return valg.aktiviteter.includes(it);
  })) {
    return [];
  }

  var skredTing = ['Skredsker', 'Skestang', 'Spade'];
  return stringArrayToItems(skredTing, Kategori.TekniskUtstyr);
}